/// Базовые цвета https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                hsl(0, 0%, 10%);
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 hsl(0, 0%, 30%);
$gray-dark:                   hsl(0, 0%, 40%);
$gray:                        hsl(0, 0%, 50%);
$gray-light:                  hsl(0, 0%, 80%);
$gray-lighten:                hsl(0, 0%, 85%);
$gray-lighter:                hsl(0, 0%, 90%);
$gray-lightest:               hsl(0, 0%, 95%);
$white:                       hsl(0, 0%, 100%);
$color-main--lightest:        hsl(203, 59%, 92%);
$color-main--lighter:         hsl(204, 46%, 77%);
$color-main:                  hsl(203, 47%, 66%);
$color-main--darker:          hsl(203, 42%, 58%);
$color-main--darkest:         hsl(202, 44%, 54%);
$color-success:               hsl(120, 39%, 54%);
$color-danger:                hsl(2, 64%, 58%);
$color-warning:               hsl(35, 84%, 62%);

$color-grey-red--lighter:     hsl(30, 7%, 89%);
$color-grey-red:              hsl(23, 15%, 40%);
$color-grey-red--darker:      hsl(23, 18%, 32%);
// Семантические цвета

$text-color:                  $gray-darkest;
$text-color--muted:           $gray;
$body-bg:                     $white;

$link-color:                  $color-main;
$link-color--hover:           darken($color-main, 15%);

$border-color:                $gray-light;



// Базовая типографика

$font-size--normal:           0.875rem;
$font-size--bigger:           1.125rem;
$font-size--biggest:          1.3125rem;


$font-size--h1:               2.25rem;
$font-size--h2:               1.125rem;
$font-size--h3:               1.5rem;
$font-size--small:            0.75rem;
// 14-0.875, 18-1.125, 21-1.3125, 30-1.875

$line-height:                 1.375rem;

$font-family:                 'PT Sans', -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
$font-family--serif:          'Georgia', 'Times New Roman', 'Times', serif;
$font-family--monospace:      'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace;
$font-family--headings:       $font-family;



// Ширины

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   992px;
$screen-xl:                   1200px;
$screen-xxl:                  1800px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                $screen-lg - 30;
$container-xl:                $screen-xl - 30;
$container-xxl:               $screen-xxl - 30;



// Модульная сетка

$grid-columns:                12;
$grid-gutter-width:           30px;



// Разное

$field-padding-vertical:      0.3em;
$field-padding-horizontal:    0.7em;

$border-radius:               3px;
$opacity:                     0.7;
$transition-time:             0.3s;
$shadow:                      0 4px 2px -2px rgba(0, 0, 0, 0.3);

$menu-desktop-width:          $screen-xl;
$toggler-part-height:         3px;
$toggler-part-bg-color:       #000;

$z-index-modal:               100;
