.icon {
  fill: #81b3d2;
  width: 36px;
  // width: 100%;
	height: 36px;
}

.icon_wrapper {
  margin-left: 1.4rem;
}

@media screen and (min-width: 48rem) {

  .icon_wrapper {
    margin-left: 0;
  }

  .icon {
    width: 72px;
    height: 72px;
  }
}
